@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap");
@import url("../vendor/fontawesome/css/all.min.css");
/* Code Ex: backgroundShadowCreate(3px, 6px, rgba(0,0,0,0.15)) */
@media (min-width: 1024px) and (max-width: 1100px) {
  .csm-slider-1 .csms1-text-1 {
    font-size: 60px;
  }
}
@media (max-width: 1023px) {
  .csm-body-ovf-1 {
    overflow: hidden;
  }
  .csm-header-action-m-1 .csmham1-item-1 {
    margin-right: 10px;
  }
  .csm-header-action-m-1 .csmham1-item-1:last-child {
    display: inline-flex;
    max-width: 40px;
    width: 100%;
  }
  .csm-header-action-m-1 .csmham1-item-1:nth-child(2) {
    margin-right: 10px;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .csm-header-1 {
    padding: 5px 10px;
  }
  .csm-btn-1, .csm-btn-2 {
    min-height: 35px;
    font-size: 14px;
  }
  .csm-slider-1 .csms1-text-1 {
    font-size: 40px;
  }
  .csm-slider-1 .csms1-text-2 {
    font-size: 18px;
  }
}
@media (max-width: 767px) {
  .csm-header-1 {
    padding: 8px 10px;
  }
  .csm-header-action-m-1 .csm-btn-1, .csm-header-action-m-1 .csm-btn-2 {
    min-height: 40px;
    min-width: 40px;
  }
  .csm-header-action-m-1 .csm-btn-1 i, .csm-header-action-m-1 .csm-btn-2 i {
    display: block;
    padding-right: 0px;
  }
  .csm-header-action-m-1 .csm-btn-1 span, .csm-header-action-m-1 .csm-btn-2 span {
    display: none;
  }
  .csm-mobile-menu-d-1 {
    top: 85px;
  }
  .csm-mobile-menu-d-1.active {
    height: calc(100vh - 86px);
  }
  .csm-slider-1 .csms1-text-1 {
    font-size: 28px;
  }
  .csm-slider-1 .csms1-text-2 {
    font-size: 14px;
  }
  .csm-slider-1 .csms1-bg-d-1 {
    background-position: 75% !important;
  }
  .csm-slider-1.owl-theme .owl-nav.disabled + .owl-dots {
    bottom: 45px;
  }
  .csm-btn-3, .csm-btn-4 {
    min-height: 40px;
  }
  .csm-wave-d-1 {
    height: 40px;
  }
}